import axios from "axios"

import Config from "./Config"
import {
  Decrypt,
  Encrypt
} from "./Des";

export function Http_Post(param, successCall, errorCall) {
  // json
  let paramJson = JSON.stringify(param)
  // 
  // let encrypt = Encrypt(paramJson, config.key);
  // 
  let params = new URLSearchParams();
  // params.append("signs", encrypt);
  params.append("signs", paramJson);
  params.append("timestamps", Date.now());
  if (sessionStorage.getItem("user") == null) {
    params.append("userCode", "");
  } else {
    params.append("userCode", JSON.parse(sessionStorage.getItem("user")).code);
  }


  return axios.post(Config.SERVICE_URL, params)
    .then(function (response) {
      // let decrypt = Decrypt(response.data.signs, config.key);
      // successCall(JSON.parse(decrypt))
      successCall(JSON.parse(response.data.signs))
    }).catch(function (error) {
      errorCall(error)
    })
}

export function Http_Get() {
  return axios.get(Config.SERVICE_URL)
}
